
import Blur from 'vux/src/components/blur/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Alert from 'vux/src/components/alert/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import PullTo from 'vue-pull-to'
import ZCell from '@/components/cell.vue'
export default {
directives: {
TransferDom
},
components: {
PullTo, Group, Cell, Blur, Alert, ZCell
},
data () {
return {
Loading: false,
show: false,
showHideOnBlur: false,
Logo: "http://appdev.pujiajia.com/static/mobile/images/app_ico_touch.png",
url: 'https://o3e85j0cv.qnssl.com/waterway-107810__340.jpg',//背景图片
}
},
methods: {

logOut(){
this.Loading = true;
this.show = true;
setTimeout(() => {
this.show = false;
this.$router.push("/login");
}, 1000)
},

onHide () {
this.$router.push("/login");
}
}
}



